/*eslint-disable*/

import React, { useState } from 'react';
import { TextField, Button } from '@mui/material';
import ForgotPassword from './ForgotPassword';
import RegisterImage from './Register';
import ButtonAppBar from '../AppBar/HomeAppBar';

interface SignInProps {
  getSignUp: () => void;
}

export default function SignIn(props: SignInProps) {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [showForgotPassword, setShowForgotPassword] = useState(false);


const styles = {

  mainContainer: {
    display: 'flex',
    background: '#e6e6e6',
  },

  registerContent: {
    marginLeft: '5vw',
    width: '35vw',
   
  },
};


  const handleForgotPasswordClick = () => {
    setShowForgotPassword(true);
  };

  const handleForgotPasswordClose = () => {
    setShowForgotPassword(false);
  };

  const handleSubmit = (event: any) => {
    event.preventDefault();
    console.log(email, password);
  };

  return (
    <React.Fragment>
      <ButtonAppBar />
      <div  style={styles.mainContainer}>
       
          <RegisterImage />
       
        <div style={styles.registerContent}>
          <h2>SIGN IN</h2>
          <form onSubmit={handleSubmit} action="/login">
            <TextField
              type="email"
              variant="standard"
              label="Email"
              onChange={(e) => setEmail(e.target.value)}
              value={email}
              fullWidth
              required
              sx={{ "& .MuiFormLabel-root.Mui-focused": { color: 'black' }, mb: 4 }}   
            />

            <TextField
              type="password"
              variant="standard"
              label="Password"
              onChange={(e) => setPassword(e.target.value)}
              value={password}
              required
              fullWidth
              sx={{ "& .MuiFormLabel-root.Mui-focused": { color: 'black' }, mb: 4}} 
            />
            <a style={{ cursor: 'pointer', color: '#0066cc', fontSize: '13px' }} onClick={handleForgotPasswordClick}>
              Forgot Password?
            </a>

            <ForgotPassword open={showForgotPassword} onClose={handleForgotPasswordClose} />

            <Button variant="outlined"  type="submit" fullWidth href='/studentdashboard'
              style={{ background: 'black', color: 'white', marginTop: '2vh' }}
            >
              Sign IN
            </Button>

          </form>
          <small>
            Not a member?{' '}
            <a style={{ cursor: 'pointer'}} href='/signup'>
              Signup Now
            </a>
          </small></div>
      </div>
    </React.Fragment>
  );
}
