
import React, { useState } from 'react';
import { Dialog, DialogTitle, DialogContent, DialogActions, Button, TextField } from '@mui/material';

interface ForgotPasswordProps {
  open: boolean;
  onClose: () => void;
}

export default function ForgotPassword(props: ForgotPasswordProps) {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');

//   const styles = {
//     notchedOutline: {
//       borderWidth: '1px',
//       borderColor: 'yellow !important',
//     },
//   };

  const handleEmailChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setEmail(e.target.value);
  };

  const handleForgotPasswordSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    console.log('Reset password for email:', email);
    setEmail('');
    props.onClose();
  };

  return (
    <Dialog open={props.open} onClose={props.onClose}>
      <DialogTitle style={{ fontWeight:'bold' }} >Forgot Password</DialogTitle>
      <DialogContent style={{ width: '70vh' }}>
        <TextField
          autoFocus
          margin="dense"
          onChange={handleEmailChange}
          label="Email"
          type="email"
          fullWidth
          required
          variant="standard"
          color="secondary"
          sx={{ "& .MuiFormLabel-root.Mui-focused": { color: 'black' }, mb: 3}} 
        />
        <TextField
          type="password"
          variant="standard"
          color="secondary"
          label="New Password"
          onChange={(e) => setPassword(e.target.value)}
          value={password}
          required
          fullWidth
          sx={{ "& .MuiFormLabel-root.Mui-focused": { color: 'black' }, mb: 3}} 
        />

        <TextField
          type="password"
          variant="standard"
          color="secondary"
          label="Confirm Password"
          onChange={(e) => setConfirmPassword(e.target.value)}
          value={confirmPassword}
          fullWidth
          required
          sx={{ "& .MuiFormLabel-root.Mui-focused": { color: 'black' }, mb: 3}} 
        />
      </DialogContent>
      <DialogActions>
        <Button onClick={props.onClose} style={{ color: 'black', fontSize:'12px'}}>
          Cancel
        </Button>
        <Button onClick={handleForgotPasswordSubmit} variant="contained" style={{ background: 'black', fontSize:'12px'}}>
          Reset Password
        </Button>
      </DialogActions>
    </Dialog>
  );
}
