import * as React from 'react';
import { styled, useTheme, Theme, CSSObject } from '@mui/material/styles';
import Box from '@mui/material/Box';
import MuiDrawer from '@mui/material/Drawer';
import List from '@mui/material/List';
import CssBaseline from '@mui/material/CssBaseline';
import Typography from '@mui/material/Typography';
import Divider from '@mui/material/Divider';
import IconButton from '@mui/material/IconButton';

import CreativeIcon from '@rsuite/icons/Creative';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';

import Tooltip from '@mui/material/Tooltip';
import Badge from '@mui/material/Badge';
import AccountCircle from '@mui/icons-material/AccountCircle';

import LiveTvIcon from '@mui/icons-material/LiveTv';
import LocalLibraryIcon from '@mui/icons-material/LocalLibrary';
import AssignmentIcon from '@mui/icons-material/Assignment';
import AccountBalanceIcon from '@mui/icons-material/AccountBalance';
import PersonalVideoIcon from '@mui/icons-material/PersonalVideo';
import NotificationsActiveIcon from '@mui/icons-material/NotificationsActive';
import BarChartIcon from '@mui/icons-material/BarChart';
import ForumIcon from '@mui/icons-material/Forum';

// files
import AssignmentInfo from '../Student/AssignmentInfo';
import LearningInfoList from '../Student/LearningInfoList';
import PaymentGateway from '../Student/PaymentGateway';
import FileUploadComponent from '../Student/FileUpload';


interface SidenavList {
    label: string;
    icon: JSX.Element;
    content: React.ReactNode;
  }

const drawerWidth = '18vw';

const openedMixin = (theme: Theme): CSSObject => ({
  width: drawerWidth,

  transition: theme.transitions.create('width', {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.enteringScreen,
  }),
  overflowX: 'hidden',
  color:'white',
  background:'#333333',
});

const closedMixin = (theme: Theme): CSSObject => ({
  transition: theme.transitions.create('width', {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.leavingScreen,
  }),
  overflowX: 'hidden',
  color:'white',
  background:'#333333',
  width: `calc(${theme.spacing(7)} + 1px)`,
  [theme.breakpoints.up('sm')]: {
    width: `calc(${theme.spacing(8)} + 1px)`,
  },
});

const DrawerHeader = styled('div')(({ theme }) => ({
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'flex-end',
  height:'10vh',
  color:'white',
  background:'black',
  padding: theme.spacing(0, 1),
  // necessary for content to be below app bar
  ...theme.mixins.toolbar,
}));



const Drawer = styled(MuiDrawer, { shouldForwardProp: (prop) => prop !== 'open' })(
  ({ theme, open }) => ({
    width: drawerWidth,
    flexShrink: 0,
    whiteSpace: 'nowrap',
    boxSizing: 'border-box',
    ...(open && {
      ...openedMixin(theme),
      '& .MuiDrawer-paper': openedMixin(theme),
    }),
    ...(!open && {
      ...closedMixin(theme),
      '& .MuiDrawer-paper': closedMixin(theme),
    }),
  }),
);


  
  const SidenavList: SidenavList[] = [
    {
      label: 'Learning Info',
      icon: <LocalLibraryIcon />,
      content: <LearningInfoList />,
    },
    {
      label: 'Notifications',
      icon: <NotificationsActiveIcon />,
      content: 'Add Notifications Content',
    },
    
    {
        label: 'Live Stream',
        icon: <LiveTvIcon />,
        content: 'Add Live Stream Content',
    },
    {
        label: 'Tutorials',
        icon: <PersonalVideoIcon />,
        content: <FileUploadComponent />,
    },
    {
        label: 'Assignments',
        icon: <AssignmentIcon />,
        content: <AssignmentInfo />,
    },
    {
        label: 'Attendance',
        icon: <BarChartIcon />,
        content: 'Add Attendance Content',
    },
    {
        label: 'Payment',
        icon: <AccountBalanceIcon />,
        content: <PaymentGateway />,
    },
    {
        label: 'Feedback',
        icon: <ForumIcon />,
        content: 'Add Feedback ',
    },
  ];

  
export default function MiniDrawer() {
  const theme = useTheme();
  const [open, setOpen] = React.useState(false);
  const [hovered, setHovered] = React.useState(false);
  const [activePage, setActivePage] = React.useState(SidenavList[0]);

  const handleDrawerOpen = () => {
    setOpen(true);
  };
 

  const handlePageClick = (list: SidenavList) => {
    setActivePage(list);
  };

  const handleDrawerClose = () => {
    setOpen(false);
  };

  const handleMouseEnter = () => {
    setHovered(true);
  };

  const handleMouseLeave = () => {
    setHovered(false);
  };

  const isHovered = open || hovered;

  return (
    <Box sx={{ display: 'flex' }}>
      <CssBaseline />

       
      <Drawer variant="permanent" open={isHovered} onMouseEnter={handleMouseEnter} onMouseLeave={handleMouseLeave} >
            
            <DrawerHeader sx={{ display: 'flex', alignItems: 'center', justifyContent: 'flex-start'}}>
                <IconButton href='/' size="large" edge="start" color="inherit" sx={{ marginLeft: '0.1px' }} >
                    <CreativeIcon spin color="#f5a623"/>
                </IconButton>
                <h2 style={{ opacity: isHovered ? 1 : 0, fontFamily: 'cursive', color:'#f5a623'}} >i-ELECTRON</h2>
            </DrawerHeader>
       
        <List>
            
          {SidenavList.map((list, index) => (
            <ListItem key={list.label} disablePadding sx={{ display: 'block' }} onClick={() => handlePageClick(list)} selected={activePage === list}>
              <ListItemButton
                sx={{
                  minHeight: 48, 
                  justifyContent: isHovered ? 'initial' : 'center',
                  px: 2.5,
                  borderLeft: activePage === list ? '4px solid #e68a00' : 'inherit',
                 
                }}
              >
            
                <ListItemIcon
                  sx={{
                    minWidth: 0,
                    mr: isHovered ? 3 : 'auto',
                    justifyContent: 'center',
                    color:'white',
                    fontWeight:'bold'
                  }}
                >
                  {list.icon}
                </ListItemIcon>
                <ListItemText primary={list.label} sx={{ opacity: isHovered ? 1 : 0 }} />
              </ListItemButton>
            </ListItem>
          ))}
        </List>
        <Divider />
               
      </Drawer>
      <Box component="main" sx={{ flexGrow: 1, minWidth:'15vw' }}>
        
        <DrawerHeader sx={{ p: 1 }} > <h3>Student  </h3>
                <Tooltip title="Notification">
                <IconButton size="large" color="inherit">
                    <Badge badgeContent={4} color="error">
                        <NotificationsActiveIcon />
                    </Badge>
                </IconButton>
                </Tooltip>

                <Tooltip title="Profile">
                <IconButton size="large" color="inherit">
                    <AccountCircle />
                </IconButton>
                </Tooltip>
            
        </DrawerHeader>

        <Typography sx={{ p: 2, background:'#e6e6e6'}}>
            {activePage.content}
        </Typography>
      </Box>
    </Box>
  );
}
