import React, { useState, useEffect } from "react";
//import "./styles.css";

declare global {
  interface Window {
    googlePayClient: any;
  }
}

const baseCardPaymentMethod = {
  type: "CARD",
  parameters: {
    allowedCardNetworks: ["VISA", "MASTERCARD"],
    allowedAuthMethods: ["PAN_ONLY", "CRYPTOGRAM_3DS"],
  },
};

const googlePayBaseConfiguration = {
  apiVersion: 2,
  apiVersionMinor: 0,
  allowedPaymentMethods: [baseCardPaymentMethod],
};

export default function PaymentGateway() {
  const [gPayBtn, setGPayBtn] = useState<any>(null);

  
  function createAndAddButton() {
    if (window.googlePayClient) {
      const googlePayButton = window.googlePayClient.createButton({
        buttonColor: "default",
        buttonType: "long",
        onClick: processPayment,
      });

      setGPayBtn(googlePayButton);
    }
  }

  function processPayment() {
    console.log("test");
    const tokenizationSpecification = {
      type: "PAYMENT_GATEWAY",
      parameters: {
        gateway: "stripe",
        "stripe:version": "v3",
        "stripe:publishableKey": "pk_test_35p114pH8oNuHX72SmrvsFqh00Azv3ZaIA",
      },
    };

    const cardPaymentMethod = {
      type: "CARD",
      tokenizationSpecification: tokenizationSpecification,
      parameters: {
        allowedCardNetworks: ["VISA", "MASTERCARD"],
        allowedAuthMethods: ["PAN_ONLY", "CRYPTOGRAM_3DS"],
        billingAddressRequired: true,
        billingAddressParameters: {
          format: "FULL",
          phoneNumberRequired: true,
        },
      },
    };

    const transactionInfo = {
      totalPriceStatus: "FINAL",
      totalPrice: "123.45",
      currencyCode: "USD",
    };

    const merchantInfo = {
      merchantName: "Example Merchant Name",
    };

    const paymentDataRequest = {
      ...googlePayBaseConfiguration,
      ...{
        allowedPaymentMethods: [cardPaymentMethod],
        transactionInfo,
        merchantInfo,
      },
    };

    window.googlePayClient
      .loadPaymentData(paymentDataRequest)
      .then(function (paymentData: any) {
        console.log(paymentData);
      })
      .catch(function (err: any) {
        console.log(err);
      });
  }

  useEffect(() => {
    const initializeGooglePayClient = () => {
      window.googlePayClient = new google.payments.api.PaymentsClient({
        // Configuration options...
      });
  
      window.googlePayClient
        .isReadyToPay(googlePayBaseConfiguration)
        .then(function (response: any) {
          if (response.result) {
            createAndAddButton();
          } else {
            alert("Unable to pay using Google Pay");
          }
        })
        .catch(function (err: any) {
          console.error("Error determining readiness to use Google Pay: ", err);
        });
    };
  
    if (window.googlePayClient) {
      initializeGooglePayClient();
    } else {
      window.onload = initializeGooglePayClient;
    }
  }, []);
  

  return (
    <div className="App">
      <h1>Click the Pay button</h1>
      <div
        onClick={processPayment}
        dangerouslySetInnerHTML={{ __html: gPayBtn && gPayBtn.innerHTML }}
      />
    </div>
  );
}
