import React, {useState} from 'react';
import logo from '../../../BackgroundImage/Register.jpg';



export default function RegisterImage() {


 
  const styles = {
    mainContainer: {
      display: 'flex',
      //height:'100vh',
    },
    
    myImage: {
      width: '55vw',
      height: '87vh',
    },

    registerContent: {
    
      marginLeft: '5vw',
      width: '35vw',
      
    },
 
  };



  return ( 
    
    <><img style={styles.myImage} src={logo} alt="logo" /> </>

    );
}



