/*eslint-disable*/
import React, { useState } from 'react';
import { TextField, Button, Snackbar } from '@mui/material';
import RegisterImage from './Register';
import ButtonAppBar from '../AppBar/HomeAppBar';


interface SignUpProps {
  getSignIn: () => void;
}

export default function SignUp(props: SignUpProps) {
  const [userName, setUserName] = useState('');
  const [email, setEmail] = useState('');
  const [dateOfBirth, setDateOfBirth] = useState('');
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [openSnackbar, setOpenSnackbar] = useState(false);
  const [snackbarMessage, setSnackbarMessage] = useState('This is a notification message');

  const styles = {

    mainContainer: {
      display: 'flex',
      background: '#e6e6e6',
    },
  
    registerContent: {
      marginLeft: '5vw',
      width: '35vw',
     
    },
  };

  const handleOpenSnackbar = () => {
    setOpenSnackbar(true);
  };

  const handleCloseSnackbar = () => {
    setOpenSnackbar(false);
  };

  const handleSubmitSignUp = (event: React.FormEvent) => {
    event.preventDefault();
    console.log('SignUp details', userName, email, confirmPassword, dateOfBirth, password);

    console.log('userName.length =', userName.length);

    if (userName.length < 6) {
      console.log('userName =', userName);
      setSnackbarMessage('Username must be at least 6 characters long!');
      handleOpenSnackbar();
    } 
    else if (password !== confirmPassword) {
      console.log('password =', password);
      setSnackbarMessage('Password & Confirm password must be the same!');
      handleOpenSnackbar();
    } 
    else {
      var SignUpData: any = {};
      SignUpData.userName = userName;
      SignUpData.email = email;
      SignUpData.confirmPassword = confirmPassword;
      SignUpData.dateOfBirth = dateOfBirth;

      console.log('SignUpData =', SignUpData);
    }
  };

  return (
    <React.Fragment>
      <ButtonAppBar />
      <div  style={styles.mainContainer}>
       
          <RegisterImage />
       
        <div style={styles.registerContent}>
          
        
        <h2>SIGN UP </h2>
        <form onSubmit={handleSubmitSignUp}>
          <TextField
            type="text"
            variant="standard"
            label="User Name"
            onChange={(e) => setUserName(e.target.value)}
            value={userName}
            fullWidth
            required
            sx={{ "& .MuiFormLabel-root.Mui-focused": { color: 'black' }, mb: 3 }} 
          />

          <TextField
            type="email"
            variant="standard"
            label="Email"
            onChange={(e) => setEmail(e.target.value)}
            value={email}
            fullWidth
            required
            sx={{ "& .MuiFormLabel-root.Mui-focused": { color: 'black' }, mb: 3 }} 
          />

          <TextField
            type="password"
            variant="standard"
            label="Password"
            onChange={(e) => setPassword(e.target.value)}
            value={password}
            required
            fullWidth
            sx={{ "& .MuiFormLabel-root.Mui-focused": { color: 'black' }, mb: 3 }} 
          />

          <TextField
            type="password"
            variant="standard"
            label="Confirm Password"
            onChange={(e) => setConfirmPassword(e.target.value)}
            value={confirmPassword}
            fullWidth
            required
            sx={{ "& .MuiFormLabel-root.Mui-focused": { color: 'black' }, mb: 3 }} 
          />

          <TextField
            type="date"
            variant="standard"
            label="Date of Birth"
            onChange={(e) => setDateOfBirth(e.target.value)}
            value={dateOfBirth}
            fullWidth
            required
            InputLabelProps={{
              shrink: true,
            }}
            sx={{ "& .MuiFormLabel-root.Mui-focused": { color: 'black' }, mb: 3 }} 
          />

          <Button style={{ background: 'black', color: 'white' }} variant="outlined" color="secondary" type="submit" fullWidth>
            Sign Up
          </Button>

          <Snackbar
            open={openSnackbar}
            autoHideDuration={3000} // Duration in milliseconds
            onClose={handleCloseSnackbar}
            message={snackbarMessage}
          />
        </form>
        <small>
          Already have an account? <a style={{ cursor: 'pointer'}} href='/signin'>Signin Here</a>
        </small></div>
        </div>
    </React.Fragment>
  );
}
