import React from 'react';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import HomeDashboard from '../Views/Components/Home/HomeDashboard';
import SignIn from '../Views/Components/Register/SignIn';
import SignUp from '../Views/Components/Register/SignUp';
import StudentDashboard from '../Views/Components/Student/StudentDashboard';
import Payment from '../Views/Components/Student/PaymentGateway';


function RouteComponent() {
  return (
    <Router>

        <Routes>
            <Route path="/" element={<HomeDashboard />} />
            <Route path="/signin" element={<SignIn />} />
            <Route path="/signup" element={<SignUp />} />
            <Route path="/studentdashboard" element={<StudentDashboard />} />
            <Route path="/payment" element={<Payment />} />
                 
          </Routes>

    </Router>
    
  );
}

export default RouteComponent;
